.text-media {
	h2 {
		text-transform: uppercase;
		font-weight: 700;
		color: inherit;
		padding-bottom: @spacing-quarter;
	}
	p {
		color: @dark-color;
		text-align: center;
	}
}

.embed {
	position: relative; 
	padding-bottom: 56.25%;
	height: 0;
	overflow: hidden;
	max-width: 100%;
	height: auto;

	iframe,
	object,
	embed { 
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
	}
}
